import ranges, {Point} from "./util/ranges";
import rects from "./util/rects";

export type LogStringLevel = "error" | "warn" | "info" | "debug";

export namespace log {
	export enum LogLevel { error = 0, warn = 1, info = 2, debug = 3 }

	export interface Log {
		message: string | Error;
		level: LogLevel;
		scopes?: string[];
	}

	export interface LogReporter {
		log(log: Log): void;
	}

	type LazyString = (() => string) | string;

	export class Logger {
		protected _reporter: LogReporter;
		protected _scopes: LazyString[] = [];
		protected _level: LogLevel = LogLevel.warn;

		debug(message: LazyString, scope?: LazyString): void {
			this.log(LogLevel.debug, message, scope);
		}

		info(message: LazyString, scope?: LazyString): void {
			this.log(LogLevel.info, message, scope);
		}

		warn(message: LazyString, scope?: LazyString): void {
			this.log(LogLevel.warn, message, scope);
		}

		error(message: LazyString, scope?: LazyString): void {
			this.log(LogLevel.error, message, scope);
		}

		levelFromString(level: LogStringLevel): number {
			if (level == "error") return 0;
			if (level == "warn") return 1;
			else if (level == "info") return 2;
			else if (level == "debug") return 3;
		}

		setLevel(level: LogLevel | LogStringLevel): void {
			if (typeof level == 'string') this._level = this.levelFromString(level);
			else this._level = level;
		}

		setReporter(reporter: LogReporter): void {
			this._reporter = reporter;
		}

		addScope(scope: LazyString): void {
			this._scopes.push(scope);
		}

		removeScope(count= 1): void {
			for (let i = 0; i < count; i++) this._scopes.pop();
		}

		log(level: LogLevel, message: LazyString | Error, scope?: LazyString): void {
			if (level > this._level || !this._reporter) return;
			const log: Log = {level, message: typeof message == 'function' ? message() : message};
			if (scope) this._scopes.push(scope);
			if (this._scopes.length) log.scopes = this._scopes.map((scope) => typeof scope == 'string' ? scope : scope());
			this._reporter.log(log);
			if (scope) this._scopes.pop();
		}
	}

	export const defaultReporter = {
		log: function (log: Log): void {
			let message = log.message; //`[${Math.floor(performance.now()) / 1000}] ${log.message}`;
			if (log.scopes) message += ` (${log.scopes.join(', ')})`;
			if (log.level == LogLevel.error) console.error(message);
			else if (log.level == LogLevel.warn) console.warn(message);
			else if (log.level == LogLevel.info) console.info(message);
			else if (log.level == LogLevel.debug) console.debug(message);
		}
	};

}

export const logger = new log.Logger();
logger.setReporter(log.defaultReporter);

export namespace dom {
	let idCounter = 0;

	export function id(element?: Element): string {
		if (!element) return 'ps-' + idCounter++;
		return element.id || (element.id = 'ps-' + idCounter++);
	}

	export function docInteractive(doc: Document = document): Promise<void> {
		return new Promise<void>((resolve, reject) => {
			if (doc.readyState != 'loading') resolve();
			else doc.addEventListener('DOMContentLoaded', () => resolve());
		});
	}

	export function docComplete(doc: Document = document): Promise<void> {
		return new Promise<void>((resolve, reject) => {
			if (doc.readyState == 'complete') resolve();
			else {
				doc.defaultView.addEventListener('load', () => resolve());
				doc.defaultView.addEventListener('error', reject);
			}
		});
	}

	export function isElement(object: any): object is Element {
		return object && object.nodeType == Node.ELEMENT_NODE;
	}
}

function decamelizeMatch(match: string): string {
	return '-' + match.toLowerCase();
}

function decamelize(string: string): string {
	return string.replace(/[A-Z]/g, decamelizeMatch);
}

export namespace io {
	type Options = {[key: string]: any};
	export function parseOptionsFromElement(element: Element, defaultOptions: Options): Options {
		const options: Options = {};
		if (element.hasAttribute('ps-options')) Object.assign(options, JSON.parse(element.getAttribute('ps-options')));
		for (const key in defaultOptions) {
			const type = Array.isArray(defaultOptions[key]) ? 'array' : typeof defaultOptions[key];
			const attr = element.getAttribute('ps-' + decamelize(key));
			if (attr !== null) {
				if (type == 'boolean') options[key] = attr == 'true';
				else if (type == 'array') options[key] = attr.length ? attr.trim().split(' ') : null;
				else if (type == 'object') options[key] = JSON.parse(attr);
				else if (type == 'string') options[key] = attr;
				else if (type == 'number') options[key] = parseFloat(attr);
			}
		}

		return options;
	}

	// The fetch API is not used because it does not support the file protocol
	// TODO Error status code ?
	export function load(url: string): Promise<string> {
		return new Promise((resolve, reject) => {
			const request = new XMLHttpRequest();
			request.open('GET', url);
			request.onload = () => resolve(request.responseText);
			request.onerror = () => reject(new Error("Unable to fetch '" + url + "'"));
			request.send();
		});
	}

	// Usefull for plugins
	export function loadScript(url: string): Promise<void> {
		return new Promise((resolve, reject) => {
			const script = document.createElement('script');
			script.src = url;
			script.addEventListener('error', () => {
				reject(new Error(`Unable to load the script '${url}'.`));
			});
			script.addEventListener('load', resolve as any as EventListenerOrEventListenerObject);
			document.head.appendChild(script);
		});
	}
}

export namespace debug {
	export function drawClientRects(rects: ClientRectList | ClientRect[], doc: Document = document): void {
		for (let i = 0; i != rects.length; i++) {
			const rect = rects[i];
			drawClientRect(rect, doc);
		}
	}

	export function drawClientRect(rect: ClientRect, doc: Document = document): void {
		const view = doc.defaultView;
		drawScrollRect({
			top: rect.top + view.scrollY,
			bottom: rect.bottom + view.scrollY,
			left: rect.left + view.scrollX,
			right: rect.right + view.scrollX
		}, doc);
	}

	export function drawScrollRect(rect: rects.Rect, doc: Document = document): void {
		const rectDiv = document.createElement('div');
		rectDiv.style.position = 'absolute';
		rectDiv.style.border = '1px solid red';
		rectDiv.style.margin = rectDiv.style.padding = '0';
		rectDiv.style.top = rect.top + 'px';
		rectDiv.style.left = rect.left + 'px';
		rectDiv.style.width = (rect.right - rect.left - 2) + 'px';
		rectDiv.style.height = (rect.bottom - rect.top - 2) + 'px';
		doc.documentElement.appendChild(rectDiv);
	}

	export function drawPoint(point: Point): void {
		const view = point.container.ownerDocument.defaultView;
		let y: number;
		if (dom.isElement(point.container)) {
			const node = ranges.nodeAtPoint(point);
			if (!node) y = rects.boundingScrollRect(point.container).bottom;
			else if (dom.isElement(node)) y = rects.boundingScrollRect(node).top;
			else {
				const range = document.createRange();
				range.selectNode(node);
				y = rects.scrollRect(range.getBoundingClientRect(), view).top;
			}
		} else {
			const range = document.createRange();
			range.setStartBefore(point.container);
			range.setEnd(point.container, point.offset);
			y = rects.scrollRect(range.getBoundingClientRect(), view).bottom;
		}
		const lineDiv = document.createElement('div');
		lineDiv.style.position = 'absolute';
		lineDiv.style.background = 'red';
		lineDiv.style.height = '1px';
		lineDiv.style.left = '0';
		lineDiv.style.width = '100%';
		lineDiv.style.top = y + 'px';
		document.documentElement.appendChild(lineDiv);
	}

	export function logCaretPositionOnClick(doc: Document): void {
		if (!doc) doc = document;

		doc.addEventListener('click', (event: MouseEvent) => {
			const range = doc.caretRangeFromPoint(event.clientX, event.y);
			console.log(range.startContainer, range.startOffset);
		});

	}
}

export namespace events {
	export type Listener<K extends keyof Events, Events> = (event: Event<K, Events>, target: EventEmitter<Events>) => any;

	export type Event<K extends keyof Events, Events> = { type: K } & Events[K];

	export class EventEmitter<Events = Record<string, unknown>> {
		listeners: { [K in keyof Events]: Listener<K, Events>[] };
		on = this.addListener;

		addListener<K extends keyof Events>(eventType: K, listener: Listener<K, Events>): this {
			if (!this.listeners) this.listeners = Object.create(null);
			let lstns = this.listeners[eventType];
			if (!lstns) {
				lstns = [];
				this.listeners[eventType] = lstns;
			}
			lstns.push(listener);
			return this;
		}

		removeListener<K extends keyof Events>(eventType: K, callback: Listener<K, Events>): this {
			const lstns = this.listeners && this.listeners[eventType];
			if (lstns) lstns.splice(lstns.indexOf(callback), 1);
			return this;
		}

		listenerCount(event: keyof Events): number {
			const lstns = this.listeners && this.listeners[event];
			return lstns ? lstns.length : 0;
		}

		emit<K extends keyof Events>(event: Event<K, Events> | K, target?: EventEmitter<Events>): any {
			const eventObj = (typeof event == 'string' ? {type: event} : event) as Event<K, Events>;
			const lstns = this.listeners && this.listeners[eventObj.type];
			if (!lstns || !lstns.length) return;

			for (const listener of lstns) {
				const result = listener.call(this, eventObj, target);
				if (result !== undefined) return result;
			}
		}
	}
}

