import {Process} from "./process";
import {events, logger, ranges, walk} from "./util";
import {Mutations} from "./util";
import {Fragmentor, LayoutProcessResult} from "./fragments";
import {LayoutProcessor, LayoutProcessorEvents, SubFragmentor, SubProcessor, SubProcessorContext, testAvoidBreakInside} from "./layout";
import {Areas} from "./layout";
import {LayoutContext} from "./layout";

class CellLayoutProcessor extends LayoutProcessor implements SubFragmentor {
	lastEvent: keyof LayoutProcessorEvents;
	limitedFloats: boolean;

	constructor(source: HTMLElement, readonly parentFragmentor: Fragmentor) {
		super(source);

		this.gcpmContext = parentFragmentor.gcpmContext;
		this.avoidBreakTypes = parentFragmentor.avoidBreakTypes;
		this._process = this.cellProcess();
	}

	* cellProcess(): Process {
		if (this.listenerCount('start')) yield 'start';
		logger.addScope(() => {
			const row = this.source.parentElement;
			const cellIndex = Array.prototype.indexOf.call(row.children, this.source);
			const rowIndex = Array.prototype.indexOf.call(row.parentNode.children, row);
			return `row ${rowIndex + 1}, cell ${cellIndex + 1}`;
		});
		const parentCtx = this.parentFragmentor.layoutContext;
		this.lastEvent = yield* this.contentsProcess(this.source, parentCtx.areas, parentCtx);
		if (this.limitedFloats) logger.info(`Some floats was deported.`);
		logger.removeScope();
		if (this.listenerCount('end')) yield 'end';
	}

	createLayoutContext(root: HTMLElement, areas: Areas, parentCtx: LayoutContext): LayoutContext {
		class CellLayoutContext extends LayoutContext {
			get bodyBottom(): number {
				return parentCtx.bodyBottom;
			}

			set bodyBottom(val) {
				parentCtx.bodyBottom = val;
			}
		}

		const ctx = new CellLayoutContext(root, parentCtx.body, areas, parentCtx);
		const floatBottomLimit = Math.min(this.source.getBoundingClientRect().bottom + ctx.bottomOffsets.get(this.source.parentElement).inside, parentCtx.bodyBottom);
		ctx.on("float-move", ({newAreaBodyRect}) => {
			if (newAreaBodyRect.bottom <= floatBottomLimit) {
				this.limitedFloats = true;
				return 'pending';
			}
		});
		return ctx;
	}

	emit<K extends keyof LayoutProcessorEvents>(event: events.Event<K, LayoutProcessorEvents> | K, target?: events.EventEmitter<LayoutProcessorEvents>): any {
		super.emit(event, target);
		const eventObj = (typeof event == 'string' ? {type: event} : event) as events.Event<K, LayoutProcessorEvents>;
		if (eventObj.type != 'start' && eventObj.type != 'end') this.parentFragmentor.emit(event, target);
	}
}

export const rowSubFragmentor: SubProcessor = function (this: Fragmentor, ctx: SubProcessorContext): false | Promise<false | LayoutProcessResult> {
	const rowType = walk.isRow(ctx.currentStyle);
	if (!rowType) return false;

	return (async () => {
		const row = ctx.currentElement;
		const cells: HTMLElement[] = [];

		for (const cell of row.children as HTMLCollectionOf<HTMLElement>) {
			if (walk.isStatic(cell)) {
				// Test the avoidBreakInside of each cells
				if (testAvoidBreakInside(row, ctx.layoutContext, this.avoidBreakTypes, ranges.positionAfter(row))) return false;
				cells.push(cell);
			}
		}

		// Style modification
		ctx.layoutContext.mutations.setAttr(row, 'ps-row', rowType);
		const verticalStyleMut = new Mutations();
		for (const cell of cells) {
			const props: { [key: string]: string } = {};
			if (rowType == 'flex-row') props['align-items'] = 'flex-start';
			else props['vertical-align'] = 'top';
			verticalStyleMut.setStyle(cell, props);
		}

		const fragmentors: Map<HTMLElement, CellLayoutProcessor> = new Map();
		let invalid = false;
		let overflow = false;
		for (const cell of cells) {
			if (walk.firstChild(cell, walk.isStaticNode)) {
				const fragmentor = new CellLayoutProcessor(cell, this);
				fragmentors.set(cell, fragmentor);
				await fragmentor.start().ended;
				if (fragmentor.lastEvent == 'no-break-point') {
					invalid = true;
					break;
				} else if (fragmentor.lastEvent != 'no-overflow') {
					overflow = true;
				}
			}
		}

		verticalStyleMut.revert();

		if (invalid) {
			for (const cell of cells) if (fragmentors.has(cell)) fragmentors.get(cell).layoutContext.mutations.revert();
			return false;
		} else if (overflow) {
			const destRow = row.cloneNode(false);
			row.setAttribute('ps-breaked-after', '');
			this.layoutContext.mutations.setAttr(destRow, 'ps-breaked-before');
			this.layoutContext.mutations.insertNode(row.parentElement, destRow, row.nextSibling);

			for (const cell of cells) {
				const fragmentor = fragmentors.get(cell);
				const breakPoint = fragmentor ? fragmentor.layoutContext.breakPoint : ranges.positionAtStart(cell);
				const cellAfter = this.layoutContext.mutations.breaks(cell, breakPoint);
				destRow.appendChild(cellAfter);
				if (!walk.firstChild(cellAfter, walk.isStaticNode)) this.layoutContext.mutations.setAttr(cell, 'ps-empty-after');
			}
			return {breakPoint: ranges.positionBefore(destRow)};
		}
	})();
};


interface CellRef {
	cell: HTMLTableCellElement;
	rowSpan: number;
	colSpan: number;
}

function isCellRef(object: any): object is CellRef {
	return 'cell' in object;
}

function getCell(cell: HTMLTableCellElement | CellRef): HTMLTableCellElement {
	return isCellRef(cell) ? cell.cell : cell;
}

type TableRow = (HTMLTableCellElement | CellRef)[];

export const trWithRowSpanSubFragmentor: SubProcessor = function (this: Fragmentor, ctx: SubProcessorContext): false | Promise<false | LayoutProcessResult> {
	const trWithRowSpan = ctx.currentStyle.display == 'table-row'
		&& ctx.currentElement instanceof HTMLTableRowElement
		&& Array.prototype.some.call(ctx.currentElement.cells, (cell: HTMLTableCellElement) => cell.rowSpan > 1);
	if (!trWithRowSpan) return false;

	return (async () => {
		ctx.currentElement.setAttribute('ps-row', ctx.currentStyle.display);
		ctx.currentBox.mainBlock = null;

		const rows: TableRow[] = [];
		let currentTR = ctx.currentElement as HTMLTableRowElement;
		const columnCount = Array.prototype.reduce.call(currentTR.cells, (value: number, cell: HTMLTableCellElement) => value + cell.colSpan, 0);
		const rowSpans: Map<number, CellRef> = new Map();
		const trByRow: Map<TableRow, HTMLTableRowElement> = new Map();
		const staticTr = walk.predicate(walk.isConstructedBy(HTMLTableRowElement), walk.isStatic);
		do {
			let iCell = 0;
			const row: TableRow = [];
			for (let iCol = 0; iCol < columnCount; iCol++) {
				const precCell = iCol > 0 && row[iCol - 1];
				if (rowSpans.has(iCol)) {
					const cellRef = Object.create(rowSpans.get(iCol));
					cellRef.rowSpan--;
					row.push(cellRef);
					if (cellRef.rowSpan == 1) rowSpans.delete(iCol);
					else rowSpans.set(iCol, cellRef);
				} else if (precCell && precCell.colSpan > 1) {
					const cellRef = {cell: getCell(precCell), rowSpan: precCell.rowSpan, colSpan: precCell.colSpan - 1};
					row.push(cellRef);
					if (precCell.rowSpan > 1) rowSpans.set(iCol, cellRef);
				} else {
					const cell = currentTR.cells[iCell];
					if (walk.isStatic(cell)) {
						row.push(cell);
						if (cell.rowSpan > 1) rowSpans.set(iCol, {cell: cell, rowSpan: cell.rowSpan, colSpan: cell.colSpan});
						iCell++;
					}
				}
			}
			// Copy of the bottomOffset for the following lines
			if (rows.length) ctx.layoutContext.bottomOffsets.set(currentTR, ctx.layoutContext.bottomOffsets.get(ctx.currentElement));
			rows.push(row);
			trByRow.set(row, currentTR);
			currentTR = walk.nextSibling(currentTR, staticTr);
		} while (currentTR && rowSpans.size);

		const lastRow = trByRow.get(rows[rows.length - 1]);
		if (testAvoidBreakInside(lastRow.parentElement, ctx.layoutContext, this.avoidBreakTypes, ranges.positionAfter(lastRow))) {
			ctx.nodes.currentNode = lastRow;
			return false;
		}

		let precRow = null;
		const pendingSpans = [];
		const fragmentors: Map<HTMLTableCellElement, CellLayoutProcessor> = new Map();
		for (let iRow=0; iRow<rows.length; iRow++) {
			const row = rows[iRow];
			const tr = trByRow.get(row);

			const verticalStyleMut = new Mutations();
			for (const cell of row) if (!isCellRef(cell)) verticalStyleMut.setStyle(cell, {'vertical-align': 'top'});

			let invalid = false;
			for (const cell of row) {
				if (!isCellRef(cell) && walk.firstChild(cell, walk.isStaticNode)) {
					const fragmentor = new CellLayoutProcessor(cell, this);
					fragmentors.set(cell, fragmentor);
					await fragmentor.start().ended;
					if (fragmentor.lastEvent == 'no-break-point') {
						invalid = true;
						break;
					}
				}
			}

			verticalStyleMut.revert();

			if (invalid) {
				for (const cell of row) if (!isCellRef(cell) && fragmentors.has(cell)) fragmentors.get(cell).layoutContext.mutations.revert();
				if (precRow) {
					// Tests the break inside before the invalid row. Implies to revert all the rows in this case.
					if (testAvoidBreakInside(tr.parentElement, ctx.layoutContext, this.avoidBreakTypes, ranges.positionBefore(tr), true)) {
						for (let i=iRow - 1; i>=0; i--) {
							const rowToRevert = rows[i];
							for (const cell of rowToRevert) if (!isCellRef(cell) && fragmentors.has(cell)) fragmentors.get(cell).layoutContext.mutations.revert();
						}
						ctx.nodes.currentNode = lastRow;
						return false;
					} else {
						insertRowSpans.call(this, row, tr, fragmentors);
						if (pendingSpans.length) {
							return {breakPoint: ranges.positionBefore(tr)};
						} else {
							ctx.nodes.currentNode = tr;
						}
					}
				} else {
					ctx.nodes.currentNode = trByRow.get(rows[rows.length - 1]);
				}
				return false;
			} else {
				let overflow = false;
				for (const cell of row) {
					if (cell.rowSpan > 1) pendingSpans.push(getCell(cell));
					else {
						const fragmentor = fragmentors.get(getCell(cell));
						if (fragmentor && fragmentor.lastEvent != 'no-overflow') {
							overflow = true;
							pendingSpans.length = 0;
							break;
						}
					}
				}

				// TODO Use breakAtPoint to duplicate for the row ?
				if (overflow && !pendingSpans.length) {
					const destRow = tr.cloneNode(false);
					tr.setAttribute('ps-breaked-after', '');
					this.layoutContext.mutations.setAttr(destRow, 'ps-breaked-before');
					this.layoutContext.mutations.insertNode(tr.parentElement, destRow, tr.nextSibling);
					tr.parentNode.insertBefore(destRow, tr.nextSibling);
					for (const cell of row) {
						const baseCell = getCell(cell);
						const fragmentor = fragmentors.get(baseCell);
						const breakPoint = fragmentor ? fragmentor.layoutContext.breakPoint : ranges.positionAtStart(baseCell);
						const cellAfter = this.layoutContext.mutations.breaks(baseCell, breakPoint);
						this.layoutContext.mutations.setProp(cellAfter, 'rowSpan', cell.rowSpan);
						cell.rowSpan = 1;
						destRow.appendChild(cellAfter);
						if (!walk.firstChild(cellAfter, walk.isStaticNode)) this.layoutContext.mutations.setAttr(baseCell, 'ps-empty-after');
					}
					return {breakPoint: ranges.positionBefore(destRow)};
				}
			}
			precRow = row;
			ctx.nodes.currentNode = trByRow.get(row);
		}
	})();
};

function insertRowSpans(this: Fragmentor, row: TableRow, tr: HTMLTableRowElement, fragmentors: Map<HTMLTableCellElement, CellLayoutProcessor>): void {
	let insertRef: HTMLTableCellElement = null;
	for (let iCell = row.length - 1; iCell >= 0; iCell--) {
		let cell = row[iCell];
		if (isCellRef(cell)) {
			if (cell.cell.rowSpan > 1) {
				// In the case of a cellRef with a colspan, we iterate to the first cellRef of this cell
				let precCell = iCell > 0 && row[iCell - 1];
				while (precCell && isCellRef(precCell) && precCell.cell == cell.cell) {
					iCell--;
					cell = precCell;
					precCell = iCell > 0 && row[iCell - 1];
				}
				const fragmentor = fragmentors && fragmentors.get(cell.cell);
				const breakPoint = fragmentor ? fragmentor.layoutContext.breakPoint : ranges.positionAtEnd(cell.cell);
				const cellAfter = this.layoutContext.mutations.breaks(cell.cell, breakPoint);
				this.layoutContext.mutations.setProp(cellAfter, 'rowSpan', cell.cell.rowSpan - cell.rowSpan);
				cellAfter.rowSpan = cell.rowSpan;
				tr.insertBefore(cellAfter, insertRef);
				if (!walk.firstChild(cellAfter, walk.isStaticNode)) this.layoutContext.mutations.setAttr(cell.cell, 'ps-empty-after');
				insertRef = cellAfter;
			} else {
				insertRef = cell.cell;
			}
		} else {
			insertRef = cell;
		}
	}
}
